.section {
    margin-top: 120px;
}

.single-product .left-image {
    margin-right: 80px;
}

.single-product .left-image img {
    border-radius: 25px;
    width: 100%;
    overflow: hidden;
}

.single-product h4 {
    font-size: 27px;
    margin-bottom: 20px;
    font-weight: 900;
}

.single-product span.price {
    font-size: 27px;
    font-weight: 700;
    color: var(--bg-primary);
}

.single-product span.price em {
    font-style: normal;
    color: #c8c8c8;
    font-size: 17px;
    text-decoration: line-through;
    font-weight: 500;
    margin-right: 5px;
}

.single-product p {
    margin-top: 50px;
    margin-bottom: 60px;
    font-size: 14px;
    line-height: 28px;
}

.single-product form {
    display: inline;
}

.single-product form input {
    width: 80px;
    height: 50px;
    background-color: #f7f7f7;
    border: 1px solid #e7e7e7;
    border-radius: 25px;
    text-align: center;
    float: left;
    margin-right: 15px;
}

.single-product form button {
    display: inline-block;
    height: 50px;
    line-height: 50px;
    background-color: var(--bg-btn);
    color: var(--color-white);
    font-size: 15px;
    text-transform: uppercase;
    font-weight: 500;
    padding: 0px 25px;
    border: none;
    border-radius: 25px;
    transition: var(--transition);
}

.single-product form button:hover {
    background-color: var(--bg-primary);
    transition: var(--transition);
}

.single-product form button i {
    margin-right: 5px;
}

.single-product ul {
    margin-top: 60px;
}

.single-product ul li {
    display: block;
    margin: 20px 0px;
    font-size: 14px;
    color: var(--bg-primary);
}

.single-product ul li span {
    width: 120px;
    display: inline-block;
    color: #aaa;
}

.single-product .sep {
    width: 100%;
    height: 1px;
    background-color: #eee;
    margin: 80px 0px;
}

/* Section2 */
.tabs-content {
    background-color: #f7f7f7;
    border-radius: 25px;
    padding: 60px;
    transition: all 0.3s;
}

.tabs-content ul.nav-tabs {
    border-bottom: none;
}

ul,
li {
    padding: 0;
    margin: 0;
    list-style: none;
}

.tabs-content ul.nav-tabs li {
    border-right: 1px solid #d7d7d7;
    margin-right: 30px;
    padding-right: 30px;
}

.tabs-content .tab-content {
    margin-top: 30px;
}

.tabs-content ul.nav-tabs li:last-child {
    border-right: none;
    margin-right: 0px;
    padding-right: 0px;
}

.tabs-content .nav-link {
    border: none;
    border-radius: 0px;
    background-color: transparent !important;
    font-size: 20px;
    font-weight: 600;
    color: #1e1e1e;
    padding: 0px;
}

p {
    font-size: 14px;
    line-height: 28px;
    color: #4a4a4a;
}

.more-info .nav-item .active {
    color: var(--bg-primary) !important;
}